/* Reset
  =============== */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

address {
  font-style: normal; }

figure {
  margin: 0; }

input[type=search] {
  box-sizing: border-box; }

