@media (max-width: 1200px) {
  #photos {
    column-count: 4 !important; } }

@media (max-width: 1000px) {
  #photos {
    column-count: 3 !important; } }

@media (max-width: 800px) {
  #photos {
    column-count: 2 !important; }
  .col-s-12 {
    width: 100% !important; } }

@media (max-width: 400px) {
  #photos {
    column-count: 1 !important; }
  .col-xs-12 {
    width: 100% !important; }
  .right {
    display: none; }
  .left {
    text-align: center; }
  .hamburger {
    display: block !important;
    float: right; }
  .sidenav {
    padding-top: 15px; }
  .sidenav a {
    font-size: 18px; } }

nav {
  background-color: #adadff;
  font-family: 'Roboto', sans-serif;
  border-bottom: 5px yellow dashed; }
  nav .wrapper {
    margin: 0px 10%; }
  nav a, nav img {
    padding: 0px; }
  nav img {
    width: 30%; }
  nav ul {
    width: 100%;
    margin: 0;
    list-style: none; }
    nav ul:after {
      content: "";
      display: block;
      clear: both; }
  nav li {
    float: right;
    list-style: none; }
  nav .left {
    float: left !important;
    font-weight: bold; }
  nav a {
    display: block;
    text-align: center;
    color: white;
    padding: 10px;
    text-decoration: none !important;
    font-size: 18px; }
    nav a:hover {
      background-color: #b3b3ff;
      color: #ffff29; }

#page-banner {
  position: relative;
  width: 100%; }
  #page-banner .responsive-img {
    width: 100%; }
  #page-banner .container {
    padding: 2% 5%; }
  #page-banner .parallax {
    /* The image used */
    background-image: url("../../dist/images/banner.jpg");
    /* Set a specific height */
    height: 500px;
    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; }
  #page-banner .container {
    background-color: #9999ff;
    padding: 1% 2% 1% 2%;
    margin-top: 0px !important;
    color: white;
    position: absolute;
    border-radius: 5px;
    border: 2px dashed white;
    top: 200px;
    left: 2%; }
    #page-banner .container h1 {
      font-family: 'Baloo Bhaina', cursive;
      font-weight: bold;
      font-size: 50px;
      margin: 0px;
      line-height: 4rem; }
    #page-banner .container h3 {
      font-family: 'Baloo Bhaina', cursive;
      margin: 0px; }
    #page-banner .container hr {
      margin: 0px; }

p {
  letter-spacing: 1px;
  word-spacing: 1px;
  line-height: 1.5rem; }

hr {
  padding: 0;
  border: none;
  border-top: medium double white;
  color: #white;
  text-align: center; }

#about .section-head, #cats .section-head, #gallery .section-head {
  background-color: #d6d6ff;
  padding: 1% 5%; }
  #about .section-head h1, #cats .section-head h1, #gallery .section-head h1 {
    text-align: center;
    color: #ffff2e;
    font-size: 30px;
    margin: 0px;
    font-family: 'Baloo Bhaina', cursive; }
  #about .section-head h5, #cats .section-head h5, #gallery .section-head h5 {
    text-align: center;
    color: white;
    margin: 0px;
    padding-left: 5px;
    padding-bottom: 5px;
    margin-bottom: 5px;
    font-family: 'Ubuntu', sans-serif; }

#about .section-body, #cats .section-body, #gallery .section-body {
  background-color: #fafaff;
  font-family: 'Roboto', sans-serif;
  color: #3a3a3a; }

#about .container, #cats .container, #gallery .container {
  padding: 2% 5%; }

#about .responsive-img, #cats .responsive-img, #gallery .responsive-img {
  width: 100%; }

#about .row::after, #cats .row::after, #gallery .row::after {
  content: "";
  clear: both;
  display: block; }

#about .col-6, #cats .col-6, #gallery .col-6 {
  float: left;
  width: 50%; }

#cats {
  margin-top: 5px; }
  #cats .col-4 {
    float: left;
    width: 33.33%; }
  #cats .col-4 {
    text-align: center; }
  #cats .cats-img {
    border-radius: 50%;
    border: 5px dashed #ccccff;
    width: 60%;
    margin: 0 auto; }
  #cats p {
    text-align: center; }
  #cats span {
    font-family: 'Baloo Bhaina', cursive;
    font-size: 20px;
    color: #a3a3ff; }

#gallery {
  margin-top: 5px; }
  #gallery .col-2 {
    float: left;
    width: 16.66%; }
  #gallery #photos {
    line-height: 0;
    column-count: 5;
    column-gap: 0px; }
  #gallery img {
    width: 100% !important;
    height: auto;
    margin: 0 auto; }

#sub-footer {
  background-color: #e6e6ff; }
  #sub-footer .container {
    padding: 2% 5%; }
  #sub-footer input, #sub-footer textarea {
    border-radius: 3px;
    border: 1px lightgray solid;
    margin: 1%;
    font-family: 'Roboto', sans-serif;
    width: 100%; }
  #sub-footer input {
    line-height: 2rem; }
  #sub-footer label {
    margin-left: 1%;
    font-family: 'Roboto', sans-serif;
    color: #3a3a3a;
    padding-top: 20px;
    font-size: 12px; }
  #sub-footer .btn {
    background-color: #a3a3ff;
    border-radius: 5px;
    border: 2px solid white;
    color: white;
    font-size: 15px;
    font-family: 'Baloo Bhaina', cursive;
    padding: 0 10px; }
    #sub-footer .btn:hover {
      background-color: white;
      color: #a3a3ff;
      transition: 1s;
      transition-timing-function: ease; }
  #sub-footer .col-6 {
    float: left;
    width: 50%; }
  #sub-footer .col-5 {
    float: left;
    width: 41.66%; }
  #sub-footer .col-12 {
    float: left;
    width: 100%; }
  #sub-footer .row::after {
    content: "";
    clear: both;
    display: block; }

#subfooter-header {
  font-family: 'Baloo Bhaina', cursive;
  background-color: #c2c2ff;
  border-bottom: 3px dashed white; }
  #subfooter-header h1 {
    margin: 0;
    color: #ffff2e; }
  #subfooter-header h5 {
    color: white;
    margin: 0px;
    border-left: 3px solid white;
    padding-left: 5px;
    font-family: 'Ubuntu', sans-serif; }

#footer {
  border-top: 2px yellow solid;
  background-color: #9e9eff;
  font-family: 'Roboto', sans-serif; }
  #footer .col-6 {
    float: left;
    width: 50%; }
  #footer .container {
    padding: 2% 5%; }
  #footer .row::after {
    content: "";
    clear: both;
    display: block; }
  #footer h3 {
    color: white;
    margin: 0;
    font-family: 'Ubuntu', sans-serif; }
  #footer i {
    font-size: 30px; }
  #footer p {
    padding-left: 5px;
    color: #3a3a3a;
    margin: 0;
    font-size: 14px; }
  #footer a {
    color: #3a3a3a; }
    #footer a:hover {
      color: #ffff1a; }

#footer-footer {
  background-color: #9999ff; }
  #footer-footer h4 {
    margin: 0;
    padding: 10px;
    text-align: center;
    color: #e6e6ff;
    font-family: 'Baloo Bhaina', cursive;
    font-size: 14px; }
